import axios from "axios";
import { useState, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import BarLoader from "../../Components/Common/BarLoader";
import ApiHeader from "../../Components/ApiList/ApiHeader";
import PropertyApiList from "../../Components/ApiList/PropertyApiList";
import PaymentCard from "./PaymentCard";
import CitizenApplyApiList from "../../Components/CitizenApplyApiList";
import BrandLoader from "../../Components/Common/BrandLoader";
import CommonModal from "../../Components/GlobalData/CommonModal";
import ServerErrorCard from "../AdvertisementModule/ServerErrorCard";
import { nullToNA } from "../../Components/Common/PowerUps/PowerupFunctions";
import useSetTitle from "../../Components/GlobalData/UseSetTitle";
import AxiosInterceptors from "../../Components/GlobalData/AxiosInterceptors";

function PropertyPayment(props) {
    const [isLoading, setisLoading] = useState(false);
    const [isLoading2, setisLoading2] = useState(false);
    const [applicationFullData, setapplicationFullData] = useState()
    const [demandDetail, setdemandDetail] = useState()
    const [fullData, setfullData] = useState()
    const [loader, setLoader] = useState(false) // Used when click on Pay Now
    const [erroState, seterroState] = useState(false);
    const [tranNo, settranNo] = useState(null)
    const [payAmount, setpayAmount] = useState(null)


    const { id, moduleType } = useParams()

    const navigate = useNavigate()

    const { zeroDemandPayment, api_getDirectHoldingDemandById, api_getHoldingDemandById, api_getsafDemandById } = CitizenApplyApiList();

    const fetchDemandDetail = (fQtr = null, fYear = null) => {
        seterroState(false)
        let url
        let requestBody

        if (moduleType == 'holding') {
            url = api_getHoldingDemandById
            requestBody = {
                propId: id,
                fYear: fYear,
                qtr: fQtr
            }
        }
        if (moduleType == 'saf') {
            url = api_getsafDemandById
            requestBody = {
                id: id
            }
        }
        if (moduleType == 'direct') {
            url = api_getDirectHoldingDemandById
            requestBody = {
                propId: id,
                fYear: fYear,
                qtr: fQtr
            }
        }


        if (fQtr !== null) {
            setisLoading2(true)
        } else {
            setisLoading(true)
        }

        console.log("before hit => ", requestBody, url)

        AxiosInterceptors.post(url, requestBody, ApiHeader())
            .then(function (response) {
                console.log('payment details data...', response?.data)
                setdemandDetail(response?.data?.data)
                setfullData(response?.data)
                if (response?.data?.data?.paymentStatus == 1) {
                    navigate(`/viewDemand/${id}`)
                }
                if (fQtr !== null) {
                    setisLoading2(false)
                } else {
                    setisLoading(false)
                }
                setpayAmount(response?.data?.data?.amounts?.isPayable)
                if (response?.data?.data?.amounts?.isPayable == false) {
                    submitDemand()
                }
            })
            .catch(function (error) {
                console.log('payment details error...', error)
                seterroState(true)
                if (fQtr !== null) {
                    setisLoading2(false)
                } else {
                    setisLoading(false)
                }
            })
    }

    const submitDemand = () => {

        setisLoading(true)
        seterroState(false)

        let body = {
            id: id,
            paymentMode: "Online"
        }

        AxiosInterceptors.post(zeroDemandPayment, body, ApiHeader())
            .then((res) => {
                console.log('response zero payment => ', res)
                if (res?.data?.status) {
                    console.log('response zero payment => ', res?.data?.data)
                    settranNo(res?.data?.data?.TransactionNo)
                } else {
                    console.log('false status response zero payment => ', res)
                    seterroState(true)
                }
            })
            .catch((err) => {
                console.log('error zero payment => ', err)
                seterroState(true)
            })
            .finally(() => setisLoading(false))
    }

    useEffect(() => {
        fetchDemandDetail()
    }, [id, moduleType])

    if (isLoading) {
        return (
            <>
                <BrandLoader />
                <div className='min-h-screen'></div>
            </>
        )
    }
    if (erroState) {
        return (
            // <CommonModal>
            <ServerErrorCard title="Server is busy" desc="Server is too busy to respond. Please try again later." buttonText="View Dashboard" buttonUrl="/propertyDashboard" />
            // </CommonModal>
        )
    }

    const navigateReciept = () => {
        moduleType != 'direct' && navigate(`/paymentReceipt/${tranNo}/${moduleType == "saf" ? "saf" : "holding"}`)
        moduleType == 'direct' && navigate(`/paymentReceipt/direct/${tranNo}/${moduleType == "saf" ? "saf" : "holding"}`)
    }

    return (
        <>
            {isLoading2 && <BarLoader />}
            {/* <div className=" font-bold text-2xl pb-4 md:py-4">Holding Deactivation</div> */}

            <div className={moduleType == 'direct' && " p-4 mt-10"}>
                <div className='w-full bg-white shadow-xl mb-6'>
                    <div className='py-6 mt-2 rounded-lg shadow-lg p-4'>
                        <div className="flex flex-col md:flex-row space-y-2 md:space-x-5 pl-4 ">
                            {moduleType != 'saf' && <div className='flex-1'>
                                <div className='font-bold text-sm'>{nullToNA(demandDetail?.basicDetails?.holding_no)}</div>
                                <div className='text-gray-500 text-xs'>Holding No.</div>
                            </div>}
                            {moduleType == 'saf' && <div className='flex-1'>
                                <div className='font-bold text-sm'>{nullToNA(demandDetail?.basicDetails?.saf_no)}</div>
                                <div className='text-gray-500 text-xs'>Application No.(Saf No)</div>
                            </div>}
                            <div className='flex-1'>
                                <div className='font-bold text-sm'>{nullToNA(demandDetail?.basicDetails?.old_ward_no)}</div>
                                <div className='text-gray-500 text-xs'>Ward No.</div>
                            </div>
                            <div className='flex-1'>
                                <div className='font-semibold text-lg'>{nullToNA(demandDetail?.basicDetails?.old_ward_no)}</div>
                                <div className='text-gray-500 text-xs'>New Ward No</div>
                            </div>
                            <div className='flex-1'>
                                <div className='font-semibold text-md'>{nullToNA(demandDetail?.basicDetails?.ownership_type)}</div>
                                <div className='text-gray-500 text-xs'>Ownership Type</div>
                            </div>
                            <div className='flex-1'>
                                <div className='font-bold text-sm'>{nullToNA(demandDetail?.basicDetails?.property_type)}</div>
                                <div className='text-gray-500 text-xs'>Property Type</div>
                            </div>

                        </div>

                        <div className="flex flex-col md:flex-row space-y-2 md:space-x-10  pl-4 mt-4">
                            <div className='flex-1'>
                                <div className='font-bold text-sm'>{nullToNA(demandDetail?.basicDetails?.zone_mstr_id)}</div>
                                <div className='text-gray-500 text-xs'>Zone</div>
                            </div>
                            <div className='flex-1'>
                                <div className='font-bold text-sm'>{nullToNA(demandDetail?.basicDetails?.is_mobile_tower)}</div>
                                <div className='text-gray-500 text-xs'>Property has Mobile Tower(s) ?</div>
                            </div>
                            <div className='flex-1'>
                                <div className='font-semibold text-md'>{nullToNA(demandDetail?.basicDetails?.is_hoarding_board)} </div>
                                <div className='text-gray-500 text-xs'>Property has Hoarding Board(s) ?</div>
                            </div>
                            <div className='flex-1'>
                                <div className='font-semibold text-md'>{nullToNA(demandDetail?.basicDetails?.is_petrol_pump)}</div>
                                <div className='text-gray-500 text-xs'>Is property a Petrol Pump ?</div>
                            </div>
                            <div className='flex-1'>
                                <div className='font-bold text-sm' >{nullToNA(demandDetail?.basicDetails?.is_water_harvesting)}</div>
                                <div className='text-gray-500 text-xs'>Rainwater harvesting provision ?</div>
                            </div>
                        </div>
                    </div>

                </div>
                <div>


                    {payAmount == false ?
                        <div className="w-full h-full bg-white p-20">
                            <div>
                                <div className="text-center font-semibold text-3xl">Your application is successfully submitted !</div>
                                <div className="text-center mt-6">
                                    <button className={`mr-4 bg-indigo-500  text-white px-6 py-1 shadow-lg hover:scale-105 rounded-sm`} onClick={() => navigateReciept()}>View Receipt</button>
                                    {moduleType != "direct" && <button className={`mr-4 bg-white border border-indigo-500 text-indigo-500 px-4 py-1 shadow-lg hover:scale-105 rounded-sm`} onClick={() => navigate(`/propApplicationDetails/${id}`)}>View Application</button>}
                                </div>
                            </div>
                        </div>
                        :
                        <PaymentCard fetchDemandDetail={fetchDemandDetail} safPaymentDetailsData={demandDetail?.amounts} paymentDetails={demandDetail?.duesList} ulbId={demandDetail?.basicDetails?.ulb_id} />
                    }


                </div>
            </div>

            <div className="w-full h-40 md:none"></div>
        </>
    );
}

export default PropertyPayment;
